class Binding {
    // åˆå§‹åŒ–
    constructor() {
        this.subs = {};
    }
    // å¢žåŠ è®¢é˜…
    addSub(sub) {
        if (this.subs[sub.key]) this.subs[sub.key] = [];
        this.subs[sub.key].push(sub)
    }
    // é€šçŸ¥äº‹ä»¶
    notify() {
        this.subs.filter((item) => typeof item !== 'string').forEach((sub) => {
            sub.update()
        })
    }
}

